@font-face {
  font-family: 'Star Jedi';
  font-family: 'Star Jedi Hollow';
  font-family: 'Star Jedi Rounded';
  src: local('Star Jedi'), url('./fonts/StarJedi.ttf') format('ttf');
  src: local('Star Jedi Hollow'), url('./fonts/StarJediHollow.ttf') format('ttf');
  src: local('Star Jedi Rounded'), url('./fonts/StarJediRounded.ttf') format('ttf');
}

header {
  color: rgb(255, 255, 255);
  position:relative;
  z-index:5;
}

.header-row {
  display: flex;
  justify-content: center;
  padding-top: 20px;
}

.title1 {
  font-family: 'Star Jedi';
  font-family: 'Star Jedi Hollow';
  font-size: 3rem;
  text-shadow: 0px 0px 5px greenyellow;

}


html {
  width: 100%;
  height: 100%;
  color: white;
}

.gh-btn {
  width: 15px;
  height: 15px;
  border-radius: 40px;
  position: absolute;
  top: 6vh;
  left: 7vh;
  transform: scale(2.6);
  color: white;
  box-shadow: 0px 0px 10px greenyellow;
  background-color: rgb(139, 136, 130);
}

.gh-btn:hover {
  -moz-animation:button-twink 1s linear infinite;
  -ms-animation:button-twink 1s linear infinite;
  -o-animation:button-twink 1s linear infinite;
  -webkit-animation:button-twink 1s linear infinite;
  animation:button-twink 1s linear infinite;
}

  @keyframes button-twink {
    from { box-shadow: 0px 0px 0px greenyellow;}
    to {box-shadow: 0px 0px 15px greenyellow;}
    to {box-shadow: 0px 0px 30px greenyellow;}
  }
  @-webkit-keyframes button-twink {
    from { box-shadow: 0px 0px 10px greenyellow;}
    to {box-shadow: 0px 0px 15px greenyellow;}
  }
  @-moz-keyframes button-twink {
    from { box-shadow: 0px 0px 10px greenyellow;}
    to {box-shadow: 0px 0px 15px greenyellow;}
  }
  @-ms-keyframes button-twink {
    from { box-shadow: 0px 0px 10px greenyellow;}
    to {box-shadow: 0px 0px 15px greenyellow;}
  }

.gh-btn:active {
  box-shadow: 0px 0px 10px red;
}

.svg-inline--fa {
  vertical-align: 0.125em;
}

.game-board {
  display: inline-flex;
  width: 100vw;
  height: 70vh;
  overflow: hidden;
  flex-wrap: wrap;
}

.new-game {
  z-index: 10;
  color: white;
  position: absolute;
  top: 50vh;
  left: 50vw;
  transform: translate(-50%, -50%) !important;
  /* transform: translateX(); */
}

.new-game-wrapper {
  background-image: url(./x-wing2.gif);
  /* background-size: cover; */
  background-origin: center;
  background-position: center;
  width: 250px;
  height: 250px;
  border-radius: 400px;
  -moz-box-shadow:    inset 0 0 10px #000000;
  -webkit-box-shadow: inset 0 0 10px #000000;
  box-shadow:         inset 0 0 80px #000000,
                      inset 0 0 10px 20px #000000;
}

.new-game-btn {
  position: absolute;
  background-color: black;
  border: 2px double white;
  border-radius: 5px;
  color: white;
  font-size: 2rem;
  font-family: 'Star Jedi';
  bottom: -25vh;
  width: 500px;
  transform: translateX(-25%);
  transition: all 1s;
}

.new-game-btn:hover {
  box-shadow: 0px 0px 30px lightskyblue;
}

.new-game-btn:active {
  box-shadow: 0px 0px 30px rgb(199, 35, 35);
}
